import React from "react";

import "./Card.css";
const Card = ({ name, email, id }) => {
  return (
    <div className="tc bg-washed-red  dib br2 pa2 ma2 grow bw2 shadow-5">
      <img src={`https://robohash.org/${id}?set=set4`} alt="Kittens" />
      <div>
        <h2>{name}</h2>
        <h4>{email}</h4>
      </div>
    </div>
  );
};

export default Card;
